	module.exports = {"data":{"markdownRemark":{"html":"<h2>Rapid Application Development</h2>\n<p>Rapid Application Development (RAD) was devised as a reaction to the problems of traditional software development methodologies, particularly the problems of long development lead times.  It also addresses the problems associated with changing requirements during the development process.  </p>\n<p>The main principles of RAD are as follows:</p>\n<ol>\n<li>Incremental development. This is the main means by which RAD handles changing requirements.  Some requirements will only emerge when the users see and experience the system in use. Requirements are never seen as complete - they evolve over time due to changing circumstances.  The RAD process starts with a high-level, non-specific list of requirements which are refined during the development process.</li>\n<li>Timeboxing.  With timeboxing, the system is divided up into a number of components or timeboxes that are developed separately. The most important requirements are developed in the first timebox. Features are delivered quickly and often.</li>\n<li>The Pareto principle. Also known as the 80/20 rule, this means that around 80% of a system’s functionality can be delivered with around 20% of the total effort needed. Therefore, the last (and most complex) 20% of requirements take the most effort and time to deliver. So, you should choose as much of the 80% to deliver as possible within the first few timeboxes.  The rest, if it proves necessary, can be delivered in subsequent timeboxes.</li>\n<li>MoSCoW rules. MoSCoW is a method used for prioritising work items in software development. Items are ranked as Must Have, Should Have, Could Have or Would Like to Have. Must Have items are those which must be included in a product for it to be accepted into release, with the other classifications in descending priority.</li>\n<li>JAD workshops. Joint Application Development (JAD) is a facilitated meeting where requirements gathering is carried out, in particular interviewing users of the system to be developed. The JAD workshop usually takes place early on in the development process, although additional meetings can be organised if required later in the process.</li>\n<li>Prototyping. Building a prototype helps to establish and clarify the user requirements, and in some cases it evolves to become the system itself.</li>\n<li>Sponsor and champion. An executive sponsor is someone within the organisation who wants the system, is committed to achieving it and is prepared to fund it. A champion is someone, usually at a lower level of seniority than an executive, who is committed to the project and is prepared to drive it forward to completion.</li>\n<li>Toolsets. RAD usually adopts toolsets as a means of speeding up the development process and improving productivity. Tools are available for change control, configuration management and code reuse.</li>\n</ol>\n<h4>More Information:</h4>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Rapid_application_development\">https://en.wikipedia.org/wiki/Rapid<em>application</em>development</a> - Wikipedia article on RAD</li>\n<li><a href=\"https://www.tutorialspoint.com/sdlc/sdlc_rad_model.htm\">https://www.tutorialspoint.com/sdlc/sdlc<em>rad</em>model.htm</a> - TutorialsPoint tutorial on RAD</li>\n</ul>","fields":{"slug":"/agile/rapid-application-development/"},"frontmatter":{"title":"Rapid Application Development"}}},"pathContext":{"slug":"/agile/rapid-application-development/"}}

